import React, { Component } from "react"
import { Modal, Row, Dropdown } from "react-materialize"
import { Link } from 'react-router-dom'
import { auth, firebase } from "../../firebase"
import './StartPage.css'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Button from '@material-ui/core/Button'

class StartPage extends Component {

    state = {
        username: "",
        email: "",
        password: "",
        passwordVerify: "",
        error: null,
        difficulty: 'easy',
        catTitle: 'Random',
        category: '',
        user: ''
        }

    handleInputChange = event => {
        const { name, value } = event.target;
        this.setState({
            [name]: value
        });
    }


    handleCreateUser = event => {
        event.preventDefault();
        auth.doCreateUserWithEmailAndPassword(this.state.email, this.state.password)
            .then(() => this.setState({ username: this.state.username, email: this.state.email, password: "", error: null, loggedIn: true }))
            .catch(error => {
                this.setState({ error })
            })
            .then(() => {
                let user = firebase.auth.currentUser;
                if (user) {
                    user.updateProfile({ displayName: this.state.username })
                        .then(() => { user.sendEmailVerification() })
                        .catch(error => {
                            this.setState({ error })
                        })
                }
            })
    }

    handleUserLogin = event => {
        event.preventDefault();
        auth.doSignInWithEmailAndPassword(this.state.email, this.state.password)
            .then(() => this.setState({ username: "", email: "", password: "", error: null, loggedIn: true }))
            .catch(error => {
                this.setState({ error })
            })
    }

    handleUserLogout = event => {
        event.preventDefault();
        auth.doSignOut().then(() => this.setState({ loggedIn: false })).catch(error => { this.setState({ error }) });
    }

    handleChange = value => {
        // this.setState({ difficulty: value })
        console.log(value.currentTarget.value)
        let selection = value.currentTarget.value;
        if (selection === 0) {
            this.setState({ difficulty: 'easy' })
            // console.log(this.state.difficulty)
        } else if (selection === 1) {
            this.setState({ difficulty: 'medium' })
            // console.log(this.state.difficulty)
        } else if (selection === 2) {
            this.setState({ difficulty: 'hard' })
            // console.log(this.state.difficulty)
        }
    }

    categoryList = (value) => {
        // Code for dynamic category list
        console.log(value.currentTarget.id)
        this.setState({ category: value.currentTarget.value, catTitle: value.currentTarget.id })
        // console.log(this.state.category)
    }


    render() {
        return (
            <div className="wrapper" >
                <div>
                    <Modal
                        className="modal"
                        header={"Instructions"}
                        trigger={<a id='buttonOne' className="startButton waves-effect waves-light btn-large" href="" >Instructions</a>}>
                        <div className="modal-content">
                            <div className="modal-body">
                                <ul className="inst-body">
                                    <MenuItem>Login or create your account on the homepage</MenuItem><br />
                                    {/* <MenuItem>Invite friends to play with you!</MenuItem> */}
                                    <MenuItem>You will be shown ten questions and get ten seconds to answer each one.</MenuItem><br />
                                    <MenuItem>Choose your desired category and difficulty in the dropdown.</MenuItem><br />
                                    <MenuItem>Click "Start Game" when you're ready to begin!</MenuItem>
                                </ul>
                            </div>
                            {this.state.error && <p>{this.state.error.message}</p>}
                        </div>
                    </Modal><br />
                    <Row>
                        <Dropdown trigger={<button className='startButton waves-effect waves-light btn-large'>{this.state.catTitle}</button>} onChange={this.categoryList} category={this.state.category} s={12}>
                            {/* <Menu> */}
                                <MenuItem onClick={this.categoryList} value='0' id="Random">Random</MenuItem>
                                <MenuItem onClick={this.categoryList} value='9' id="General Knowledge">General Knowledge</MenuItem>
                                <MenuItem onClick={this.categoryList} value='10' id="Books">Books</MenuItem>
                                <MenuItem onClick={this.categoryList} value='11'id="Film">Film</MenuItem>
                                <MenuItem onClick={this.categoryList} value='12' id="Music">Music</MenuItem>
                                {/* <MenuItem  value='13'>Musicals and Theatres</MenuItem>  */}
                                <MenuItem onClick={this.categoryList} value='14' id="Television">Television</MenuItem>
                                <MenuItem onClick={this.categoryList} value='15' id="Video Games">Video Games</MenuItem>
                                <MenuItem onClick={this.categoryList} value='16' id="Board Games">Board Games</MenuItem>
                                <MenuItem onClick={this.categoryList} value='17' id="Science and Nature">Science and Nature</MenuItem>
                                <MenuItem onClick={this.categoryList} value='18' id="Computers">Computers</MenuItem>
                                {/* <MenuItem  value='19'>Mathematics</MenuItem>  */}
                                <MenuItem onClick={this.categoryList} value='20' id="Mythology">Mythology</MenuItem>
                                <MenuItem onClick={this.categoryList} value='21' id="Sports">Sports</MenuItem>
                                <MenuItem onClick={this.categoryList} value='22' id="Geography">Geography</MenuItem>
                                <MenuItem onClick={this.categoryList} value='23' id="History">History</MenuItem>
                                {/* <MenuItem  value='24'>Politics</MenuItem>  */}
                                {/* <MenuItem  value='25'>Art</MenuItem>  */}
                                <MenuItem onClick={this.categoryList} value='26' id="Celebrities">Celebrities</MenuItem>
                                {/* <MenuItem  value='27'>Animals</MenuItem>  */}
                                <MenuItem onClick={this.categoryList} value='28' id="Vehicles">Vehicles</MenuItem>
                                {/* <MenuItem  value='29'>Comics</MenuItem>  */}
                                {/* <MenuItem  value='30'>Gadgets</MenuItem>  */}
                                <MenuItem onClick={this.categoryList} value='31' id="Japanese Anime and Manga">Japanese Anime and Manga</MenuItem>
                                <MenuItem onClick={this.categoryList} value='32' id="Cartoons and Animations">Cartoons and Animations</MenuItem>
                            {/* </Menu> */}
                        </Dropdown>
                    </Row>
                    <Row>
                        <Dropdown trigger={<button className="startButton waves-effect waves-light btn-large" href="">{this.state.difficulty}</button>} s={12} type='select'>
                            <ul >
                                <MenuItem onClick={this.handleChange} id='easy'>Easy</MenuItem>
                                <MenuItem onClick={this.handleChange} value='1'>Medium</MenuItem>
                                <MenuItem onClick={this.handleChange} value='2'>Hard</MenuItem>
                            </ul>
                        </Dropdown>
                    </Row>
                    {this.props.user === null ? "" : <a className="startButton waves-effect waves-light btn-large" href="" onClick={this.handleUserLogout} >Log out</a>}
                    <Modal
                        className="font"
                        header={this.props.user === null ? 'Please create a profile below' : `Welcome ${this.props.email}`}
                        trigger={this.props.user === null ? <a className="startButton waves-effect waves-light btn-large" href="" >Create User</a> : ""}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h6 className="modal-title">Log In</h6>
                            </div>
                            <div className="modal-body">
                                <form>
                                    <input type="text" name="username" value={this.state.username} onChange={this.handleInputChange} placeholder="User Name" />
                                    <input id="txtEmail1" type="email" name="email" value={this.state.email} onChange={this.handleInputChange} autoComplete="username" />
                                    <input id="txtPassword1" type="password" name="password" value={this.state.password} onChange={this.handleInputChange} autoComplete="current-password" />
                                    {/* <input id="txtPasswordVerify" type="password" name="passwordVerify" value={this.state.passwordVerify} onChange={this.handleInputChange} placeholder="Confirm Password" /> */}
                                </form>
                            </div>

                            <div className="modal-footer">
                                {this.props.user === null ? <button id="btnLogin" onClick={this.handleCreateUser} className="btn btn-action col s10 m10" >Create User</button> :
                                    <button id="btnLogout" onClick={this.handleUserLogout} className="btn btn-action" >Log out</button>}
                            </div>
                            {this.state.error && <p>{this.state.error.message}</p>}
                        </div>
                    </Modal><br />
                    <Modal
                        className="font"
                        header={this.props.user === null ? 'Please login below' : `Welcome ${this.props.user}`}
                        trigger={this.props.user === null ? <a className="startButton waves-effect waves-light btn-large" href="" >Log in</a> : ""}>
                        <div className="modal-content">
                            <div className="modal-header">
                                <h6 className="modal-title">Log In</h6>
                            </div>
                            <div className="modal-body">
                                <form>
                                    <input id="txtEmail" type="email" name="email" value={this.state.email} onChange={this.handleInputChange} autoComplete="username" />
                                    <input id="txtPassword" type="password" name="password" value={this.state.password} onChange={this.handleInputChange} autoComplete="current-password" />
                                </form>
                            </div>

                            <div className="modal-footer">
                                {this.props.user === null ? <button id="btnLogin" onClick={this.handleUserLogin} className="btn btn-action col s10 m10" >Log in</button>
                                    : <button id="btnLogout" onClick={this.handleUserLogout} className="btn btn-action" >Log out</button>}
                            </div>
                            {this.state.error && <p>{this.state.error.message}</p>}
                        </div>
                    </Modal><br />
                    <a className="startButton waves-effect waves-light btn-large" to={{ pathname: '/game', state: { difficulty: this.state.difficulty, user: this.state.trueUser, category: this.state.category } }} >Start Game</a>
                </div>
            </div>
        );
    }
}

export default StartPage;


